.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column-reverse;
  align-items: center;
  min-height: calc(100vh - 96px - 1.5em);
  gap: 0.4em;
}

@media screen and (min-width: 600px) {
  .container {
    flex-direction: row;
    gap: 0;
  }
}
